/* Social hint dimensions */
@keyframes ripple {
  0% {
    top: 39px;
    left: 39px;
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    top: 0;
    left: 0;
    width: 77px;
    height: 77px;
    opacity: 0; } }

.loader {
  position: relative;
  width: 90px;
  height: 90px; }

.loader div {
  box-sizing: content-box;
  position: absolute;
  border-width: 3px;
  border-style: solid;
  opacity: 1;
  border-radius: 50%;
  animation: ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite; }

.loader div:nth-child(1) {
  border-color: #1184FE; }

.loader div:nth-child(2) {
  border-color: #1184FE;
  -webkit-animation-delay: -0.5s;
  animation-delay: -0.5s; }
